import { useEffect, useRef, useState } from "react";
import { useSelector } from "react-redux";
import TopLevelWindow from "../../windows/top-level-window/TopLevelWindow";
import "./GnomeWebApp.css";

function GnomeWebApp() {
  const appState = useSelector(
    (state) => state.appState.value.apps.gnomeWebApp
  );

  const [address, setAddress] = useState('https://www.bing.com/')
  const [queryAddress, setQueryAddress] = useState('https://www.bing.com/')

  const handleKeyDown = (event) => {
    if (event.code === 'Enter' || event.code === 'Return') {
      setAddress(queryAddress)
    }
  }

  const customHeaderbar = (
    <div className="webToolbar">
      <div className="btn-group" role="group">
        <button type="button" className="x-button nav-button">
          <span className="nav-icon back-nav"></span>
        </button>
        <button type="button" className="x-button nav-button">
          <span className="nav-icon forward-nav"></span>
        </button>
      </div>
      <button
        type="button"
        className="x-button nav-button"
        style={{ marginLeft: 8 }}
      >
        <span className="nav-icon refresh-nav"></span>
      </button>
      <div className="address-bar">
        <div className="address-bar-inner">
          <div className="security-icon secure"></div>
          <input
            type="text"
            className="url-input"
            defaultValue="https://www.bing.com/"
            onChange={(e) => setQueryAddress(e.target.value)}
            onKeyDown={handleKeyDown}
            onMouseMove={(e) => e.stopPropagation()}
            onDoubleClick={(e) => e.stopPropagation()}
            onMouseDown={(e) => e.stopPropagation()}
            readOnly={false}
          ></input>
        </div>
      </div>
      <button type="button" className="x-button nav-button">
        <span className="nav-icon menu-nav"></span>
      </button>
    </div>
  );
  return (
    <TopLevelWindow
      id="gnomeWebApp"
      title="Web"
      type="window"
      width={appState.width}
      height={appState.height}
      customHeaderbar={customHeaderbar}
    >
      <div className="frame">
        <iframe
          src={address}
          frameBorder="none"
          style={{ width: "100%", height: "100%", minHeight: "inherit" }}
        ></iframe>
      </div>
    </TopLevelWindow>
  );
}

export default GnomeWebApp;
