import { useSelector } from "react-redux";
import TopLevelWindow from "../../windows/top-level-window/TopLevelWindow";
import "./FilesApp.css";
import FileSystem from "../../assets/data/file-system.json";
import { useEffect, useRef, useState } from "react";

function FilesApp() {
  const [path, setPath] = useState("Home");
  const [contents, setContents] = useState([]);
  const [showBreadCrumbs, setShowBreadCrumbs] = useState(true);
  const [pathBreadCrumbs, setPathBreadCrumbs] = useState([]);

  const appState = useSelector(
    (state) => state.appState.value.apps.gnomeWebApp
  );

  const addressRef = useRef(null);

  useEffect(() => {
    let pathTokens = [];
    let pathTransformed = path.replace("~", "Home");
    if (pathTransformed.endsWith("/")) {
      setPath(pathTransformed.substring(0, pathTransformed.length - 1));
    }
    pathTokens = path.split("/");

    if (!openPath(pathTokens, FileSystem)) {
      setPath("Home");
    }
  }, [path, addressRef.current]);

  function openFile(file) {
    console.log(file);
  }

  function openPath(pathTokens, currentDirectory) {
    if (currentDirectory) {
      if (pathTokens.length == 1) {
        if (pathTokens[0] == currentDirectory.name) {
          console.log(currentDirectory.type);
          if (currentDirectory.type == "inode/directory") {
            console.log("list contents", currentDirectory.children);
            setContents(currentDirectory.children);
            if (addressRef.current) {
              addressRef.current.value = path.replace("~", "Home");
            }

            setPathBreadCrumbs(
              path.endsWith("/")
                ? path.substring(0, path.length - 1).split("/")
                : path.split("/")
            );
          } else {
            openFile(currentDirectory);
          }
        } else {
          console.warn("Path does not exist!");
          return false;
        }
      } else {
        return openPath(
          pathTokens.slice(1),
          currentDirectory.children.filter((x) => x.name == pathTokens[1])[0]
        );
      }
    } else {
      console.warn("Path does not exist!");
      return false;
    }

    return true;
  }

  const customHeaderbar = (
    <div className="webToolbar">
      <div className="btn-group" role="group">
        <button type="button" className="x-button nav-button">
          <span className="nav-icon back-nav"></span>
        </button>
        <button type="button" className="x-button nav-button">
          <span className="nav-icon forward-nav"></span>
        </button>
      </div>
      <button
        type="button"
        className="x-button nav-button"
        style={{ marginLeft: 8 }}
      >
        <span className="nav-icon refresh-nav"></span>
      </button>
      <div className="address-bar">
        <div className="address-bar-inner" style={{ cursor: "pointer" }}>
          {!showBreadCrumbs && (
            <input
              type="text"
              ref={addressRef}
              className="url-input"
              defaultValue={path}
              onKeyUp={(e) => {
                if (e.key === "Enter") {
                  setPath(e.target.value);
                }
              }}
              autoFocus
              onBlur={(e) => {
                setShowBreadCrumbs(true);
              }}
            ></input>
          )}
          {showBreadCrumbs && (
            <div
              className="path-breadcrumb-container"
              onDoubleClick={(e) => {
                setShowBreadCrumbs(false);
                e.stopPropagation();
              }}
            >
              {pathBreadCrumbs.map(function (breadCrumb, i) {
                return (
                  <div
                    key={i}
                    className="path-breadcrumb"
                    onClick={() => {
                      setPath(pathBreadCrumbs.slice(0, i + 1).join("/"));
                    }}
                  >
                    {breadCrumb}&nbsp;&rsaquo;
                  </div>
                );
              })}
            </div>
          )}
        </div>
      </div>
      <button type="button" className="x-button nav-button">
        <span className="nav-icon menu-nav"></span>
      </button>
    </div>
  );

  const sidepane = (
    <div className="files-sidepane">
      <ul>
        <li
          className={path === "Home" ? "li-selected" : ""}
          onClick={() => {
            setPath("Home");
          }}
        >
          <div className="sidepane-icon home"></div>Home
        </li>
        <li
          className={path.startsWith("Home/Desktop") ? "li-selected" : ""}
          onClick={() => {
            setPath("Home/Desktop");
          }}
        >
          <div className="sidepane-icon desktop"></div>Desktop
        </li>
        <li
          className={path.startsWith("Home/Documents") ? "li-selected" : ""}
          onClick={() => {
            setPath("Home/Documents");
          }}
        >
          <div className="sidepane-icon documents"></div>Documents
        </li>
        <li
          className={path.startsWith("Home/Pictures") ? "li-selected" : ""}
          onClick={() => {
            setPath("Home/Pictures");
          }}
        >
          <div className="sidepane-icon pictures"></div>Pictures
        </li>
        <li
          className={path.startsWith("Home/Music") ? "li-selected" : ""}
          onClick={() => {
            setPath("Home/Music");
          }}
        >
          <div className="sidepane-icon music"></div>Music
        </li>
        <li
          className={path.startsWith("Home/Videos") ? "li-selected" : ""}
          onClick={() => {
            setPath("Home/Videos");
          }}
        >
          <div className="sidepane-icon videos"></div>Videos
        </li>
        <li
          className={path.startsWith("Home/Downloads") ? "li-selected" : ""}
          onClick={() => {
            setPath("Home/Downloads");
          }}
        >
          <div className="sidepane-icon downloads"></div>Downloads
        </li>
      </ul>
    </div>
  );

  return (
    <TopLevelWindow
      id="filesApp"
      title="Files"
      type="flap-window"
      width={appState.width}
      height={appState.height}
      customHeaderbar={customHeaderbar}
      flap={sidepane}
    >
      <div className="files-outer">
        <div className="files-inner">
          <div className="row m-0 p-0" style={{ minHeight: "inherit" }}>
            {contents.map(function (icon, i) {
              return (
                <div
                  key={icon.name}
                  className="col-auto files-icon"
                  onClick={() => setPath(path + "/" + icon.name)}
                  draggable={true}
                  onDragStart={(e) => {e.dataTransfer.setData("file-name", icon.name); e.dataTransfer.setData("file-type", icon.type);}}
                >
                  <div
                    className={
                      "files-icon-image " +
                      icon.type.replace("/", "-") +
                      " " +
                      icon.name.toLowerCase().replace(".", "-")
                    }
                    style={
                      icon.type == "image/jpeg"
                        ? {
                            backgroundImage: `url(${require("../../assets/data/_Home/_Pictures/" +
                              icon.name)})`,
                          }
                        : {}
                    }
                  ></div>
                  <div className="files-name">{icon.name}</div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </TopLevelWindow>
  );
}

export default FilesApp;
