import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { closeAppMenu } from "../../redux/reducers/appMenuReducer";
import { launchApp } from "../../redux/reducers/appStatusReducer";
import "./ApplicationMenu.css";

function ApplicationMenu() {
  const open = useSelector((state) => state.appMenu.value);
  const wallpaper = useSelector((state) => state.wallpaper.value);

  const dispatch = useDispatch();
  const apps = useSelector((state) => {
    return state.appState.value.apps;
  });

  useEffect(() => {
    var previews = document.getElementsByClassName('preview-content');
    var image = require(`../../assets/data/_Home/_Pictures/${wallpaper}`)
    for (let i = 0; i < previews.length; i++) {
      previews[i].style.background = `url(${image})`
    }
  }, [wallpaper, open])
  

  return (
    <>
      {open && (
        <div
          className="application-menu"
          onClick={() => dispatch(closeAppMenu())}
        >
          <div className="container">
            <div className="row" style={{userSelect: 'none'}}>
              <div className="col-9">
                <h1>Applications</h1>
              </div>
              <div className="col-3">
                <h1>Tasks</h1>
              </div>
            </div>
            <div className="row">
              <div className="col-9">
                <table className="desktop-table">
                  <tbody>
                    <tr>
                      <td>
                        <div className="desktop-preview">
                          <div className="preview-content"></div>
                        </div>
                      </td>
                      <td>
                        <div className="desktop-preview">
                          <div className="preview-content"></div>
                        </div>
                      </td>
                      <td>
                        <div className="desktop-preview">
                          <div className="preview-content"></div>
                        </div>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <div className="col-3">
                <div className="calendar">
                  <h3 style={{ color: "#0004" }}>01:20 PM</h3>
                  <h4 style={{ opacity: 0.75 }}>Monday</h4>
                  <h6>
                    <b>July</b> 4th, 2022
                  </h6>
                </div>
              </div>
            </div>
            <br />
            <div className="row">
              <div className="col-9">
                <table className="app-grid">
                  <tbody>
                    <tr>
                      {Object.keys(apps).map((item, index) => {
                        return (
                          <td key={item}>
                            <div className={"app-menu-app-icon " + item}
                            onClick={() => dispatch(launchApp(item))}>
                              <div className="app-menu-app-name">
                                {apps[item].app_name}
                              </div>
                            </div>
                          </td>
                        );
                      })}
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  );
}

export default ApplicationMenu;
