import { createSlice } from "@reduxjs/toolkit";

export const appStateSlice = createSlice({
  name: "appState",
  initialState: {
    value: {
      fullscreen: false,
      activeWindow: "",
      apps: {
        welcomeApp: {
          app_name: "Crystal DE Tour",
          pinned: true,
          xRelative: 0.5,
          yRelative: 0.5,
          x: 0,
          y: 0,
          width: 660,
          height: 300,
          widthActual: 600,
          heightActual: 300,
          minWidth: 660,
          minHeight: 300,
          resizable: true,
          order: 1,
          fullscreen: false,
          hidden: false,
          alive: false,
          canFullScreen: true,
          canClose: true,
        },
        helpApp: {
          app_name: "Help",
          pinned: false,
          xRelative: 0.8,
          yRelative: 0.8,
          x: 0,
          y: 0,
          width: 400,
          height: 200,
          widthActual: 400,
          heightActual: 200,
          minWidth: 400,
          minHeight: 200,
          resizable: false,
          order: 0,
          fullscreen: false,
          hidden: false,
          alive: false,
          canFullScreen: false,
          canClose: false,
        },
        gnomeWebApp: {
          app_name: "Web",
          pinned: true,
          xRelative: 0.5,
          yRelative: 0.5,
          x: 0,
          y: 0,
          width: 800,
          height: 400,
          widthActual: 800,
          heightActual: 400,
          minWidth: 800,
          minHeight: 400,
          resizable: true,
          order: 2,
          fullscreen: false,
          hidden: false,
          alive: false,
          canFullScreen: true,
          canClose: true,
        },
        filesApp: {
          app_name: "Files",
          pinned: true,
          xRelative: 0.5,
          yRelative: 0.5,
          x: 0,
          y: 0,
          width: 676,
          height: 380,
          widthActual: 676,
          heightActual: 380,
          minWidth: 500,
          minHeight: 380,
          resizable: true,
          order: 2,
          fullscreen: false,
          hidden: false,
          alive: false,
          canFullScreen: true,
          canClose: true,
        },
      },
    },
  },
  reducers: {
    launchApp: (state, value) => {
      state.value.apps[value.payload].alive = true;
      state.value.apps[value.payload].hidden = false;
    },
    showApp: (state, value) => {
      state.value.apps[value.payload].hidden = false;
    },
    hideApp: (state, value) => {
      state.value.apps[value.payload].hidden = true;
    },
    setAppState: (state, value) => {
      state.value.apps[value.payload.app_name][value.payload.key] =
        value.payload.value;
    },
    setAppFocus: (state, value) => {
      state.value.activeWindow = value.payload;
      let apps = Object.keys(state.value.apps);
      apps.sort((x, y) =>
        state.value.apps[x].order < state.value.apps[y].order ? 1 : -1
      );
      let n = apps.length;
      state.value.apps[value.payload]["order"] = n;
      let z = n - 1;

      for (let i = 0; i < n; i++) {
        if (value.payload != apps[i]) {
          state.value.apps[apps[i]]["order"] = z--;
        }
      }
    },
    setAppPosition: (state, value) => {
      state.value.apps[value.payload.app_name].x = value.payload.x;
      state.value.apps[value.payload.app_name].y = value.payload.y;
    },
    setMinAppSize: (state, value) => {
      state.value.apps[value.payload.app_name].minWidth =
        value.payload.minWidth;
      state.value.apps[value.payload.app_name].minHeight =
        value.payload.minHeight;
    },
    setAppSize: (state, value) => {
      state.value.apps[value.payload.app_name].width = value.payload.width;
      state.value.apps[value.payload.app_name].height = value.payload.height;
    },
    setAppActualSize: (state, value) => {
      state.value.apps[value.payload.app_name].widthActual =
        value.payload.width;
      state.value.apps[value.payload.app_name].heightActual =
        value.payload.height;
    },
    closeApp: (state, value) => {
      state.value.apps[value.payload]["alive"] = false;

      if (value.payload == "welcomeApp") {
        state.value.apps.helpApp.alive = true;
      } else if (value.payload == "helpApp") {
        state.value.apps.welcomeApp.alive = true;
      }
    },
    toggleFullScreen: (state, value) => {
      if (state.value.apps[value.payload]["canFullScreen"])
        state.value.apps[value.payload]["fullscreen"] =
          !state.value.apps[value.payload]["fullscreen"];

      state.value.fullscreen =
        state.value.apps.welcomeApp.fullscreen ||
        state.value.apps.gnomeWebApp.fullscreen ||
        state.value.apps.filesApp.fullscreen;
    },
  },
});

export const {
  launchApp,
  showApp,
  hideApp,
  setAppState,
  setAppFocus,
  closeApp,
  setAppPosition,
  setMinAppSize,
  setAppSize,
  setAppActualSize,
  toggleFullScreen,
} = appStateSlice.actions;

export default appStateSlice.reducer;
