import { useEffect, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setWallpaper } from "../../redux/reducers/wallpaperReducer";
import "./Desktop.css";

function Desktop() {
  const wallpaper = useSelector((state) => state.wallpaper.value);
  const desktopRef = useRef(null);

  const dispatch = useDispatch();

  function handleFileDrop(e) {
    if (e.dataTransfer.getData("file-type") == 'image/jpeg') {
      dispatch(setWallpaper(e.dataTransfer.getData("file-name")))
    }
  }

  useEffect(() => {
    var image = require(`../../assets/data/_Home/_Pictures/${wallpaper}`);
    desktopRef.current.style.background = `url(${image})`;
  }, [desktopRef, wallpaper]);

  return (
    <div
      className="desktop-window"
      ref={desktopRef}
      onDrop={(e) => {
        e.preventDefault();
        console.log(e)
        handleFileDrop(e);
      }}
      onDragOver={(e) => e.preventDefault()}
    ></div>
  );
}

export default Desktop;
