import moment from "moment";
import { useRef, useState } from "react";
import { OverlayTrigger, Popover, Tooltip } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import Slider from "../../components/Slider";
import {
  closeAppMenu,
  toggleAppMenu,
} from "../../redux/reducers/appMenuReducer";
import {
  hideApp,
  launchApp,
  setAppFocus,
  showApp,
} from "../../redux/reducers/appStatusReducer";
import {
  disableCompositor,
  enableCompositor,
} from "../../redux/reducers/canvasCompositorReducer";
import "./Panel.css";

function Panel() {
  const dispatch = useDispatch();
  const appMenuOpened = useSelector((state) => state.appMenu.value);
  const fullscreen = useSelector((state) => state.appState.value.fullscreen);
  const apps = useSelector((state) => state.appState.value.apps);
  const volume = useSelector((state) => state.audioVolume.value);
  const compositionEnabled = useSelector(
    (state) => state.canvasCompositor.value
  );
  const activeWindow = useSelector(
    (state) => state.appState.value.activeWindow
  );

  const [forceOpenSidePanel, setForceOpenSidePanel] = useState(false);

  const [time, setTime] = useState(moment().format("llll"));
  setInterval(() => {
    setTime(moment().format("llll"));
  }, 10000);

  const music_popover = (
    <Popover id="popover-music">
      <Popover.Body>
        <div className="music-popover-ui"></div>
      </Popover.Body>
    </Popover>
  );

  const message_popover = (
    <Popover id="popover-message">
      <Popover.Body>
        <div className="message-popover-ui">
          
        </div>
      </Popover.Body>
    </Popover>
  );

  const bluetooth_popover = (
    <Popover id="popover-bluetooth">
      <Popover.Body>
        <div className="blutooth-popover-ui">
          <div className="menu-section">
            Bluetooth settings
          </div>
        </div>
      </Popover.Body>
    </Popover>
  );

  const wifi_popover = (
    <Popover id="popover-wifi">
      <Popover.Body>
        <div className="wifi-popover-ui" style={{marginTop: 8}}></div>
        <hr />
        <div className="menu-section">
          Turn on hotspot...
        </div>
        <div className="menu-section">
          Open network settings
        </div>
      </Popover.Body>
    </Popover>
  );

  const volume_popover = (
    <Popover id="popover-message">
      <Popover.Body>
        <div className="volume-popover-ui">
          <div className="menu-section">
            <label for="audio-output">Speakers</label>
            <Slider id="audio-output" baseClass="audio-output" defaultValue={75}/>
          </div>
          <div className="menu-section">
            <label for="audio-input">Mic</label>
            <Slider id="audio-input" baseClass="audio-input" defaultValue={50}/>
          </div>
        </div>
      </Popover.Body>
    </Popover>
  );

  const power_popover = (
    <Popover id="popover-message">
      <Popover.Body>
        <div className="power-popover-ui">
          <div className="menu-section">
            <div className="profile-section"></div>
          </div>
          <hr />
          <div className="menu-section">
            <div className="form-check form-switch">
              <input
                className="form-check-input"
                type="checkbox"
                role="switch"
                id="compositorSwitch"
                defaultChecked={compositionEnabled}
                onChange={(e) =>
                  e.target.checked
                    ? dispatch(enableCompositor())
                    : dispatch(disableCompositor())
                }
              />
              <label className="form-check-label" for="compositorSwitch">
                Canvas Compositor
              </label>
            </div>
          </div>
          <hr />
          <div className="menu-section">
            Lock
          </div>
          <div className="menu-section">
            Log Out...
          </div>
          <hr />
          <div className="menu-section">
            Suspend
          </div>
          <div className="menu-section">
            Shutdown
          </div>
        </div>
      </Popover.Body>
    </Popover>
  );

  const appTooltip = (props) => (
    <Tooltip id="button-tooltip" {...props}>
      Simple tooltip
    </Tooltip>
  );

  function activateApp(app) {
    if (!apps[app].alive) {
      dispatch(launchApp(app));
    } else if (apps[app].hidden) {
      dispatch(showApp(app));
    } else if (app != activeWindow) {
      dispatch(setAppFocus(app));
    } else {
      dispatch(hideApp(app));
    }
    dispatch(closeAppMenu());
  }

  return (
    <div className="panel-window">
      <div
        className={
          "top-panel" +
          (appMenuOpened ? " dimmed" : fullscreen ? " blackend" : "")
        }
      >
        <div
          className="date-time-string"
          onClick={() => dispatch(toggleAppMenu())}
        >
          {time}
        </div>
        <div className="dynamic-plank">
          <div className="dynamic-plank-inner">
            <div className="notification-icon"></div>
            <div className="notification-icon-area">
              <button className="background-app-notification-email">
                Email
              </button>
            </div>
            <div className="widget-area">
              <div className="music-panel-ui">
                <OverlayTrigger
                  trigger="click"
                  placement="bottom"
                  overlay={music_popover}
                  rootClose={true}
                >
                  <div className="music-panel-ui-inner">
                    <div className="album-art-small"></div>
                    <button className="music-button prev-button">
                      Previous
                    </button>
                    <button className="music-button play-button">Play</button>
                    <button className="music-button next-button">Next</button>
                    <button className="music-button stop-button">Play</button>
                  </div>
                </OverlayTrigger>
              </div>
            </div>
          </div>
        </div>
        <div className="status-icons">
          <OverlayTrigger
            trigger="click"
            placement="bottom"
            overlay={volume_popover}
            rootClose={true}
          >
            <button className={"status-icon volume-icon" + (
              (volume <= 1) ? " muted" : ((volume <= 33) ? " low" : ((volume <= 66) ? " medium" : ""))
            )}>Volume</button>
          </OverlayTrigger>
          <OverlayTrigger
            trigger="click"
            placement="bottom"
            overlay={wifi_popover}
            rootClose={true}
          >
            <button className="status-icon wifi-icon">WiFi</button>
          </OverlayTrigger>
          <OverlayTrigger
            trigger="click"
            placement="bottom"
            overlay={bluetooth_popover}
            rootClose={true}
          >
            <button className="status-icon bluetooth-icon">BlueTooth</button>
          </OverlayTrigger>
          <OverlayTrigger
            trigger="click"
            placement="bottom"
            overlay={power_popover}
            rootClose={true}
          >
            <button className="status-icon shutdown-icon">Power</button>
          </OverlayTrigger>
        </div>
      </div>
      <div
        className={
          "side-panel" +
          (appMenuOpened ? " dimmed" : "") +
          (forceOpenSidePanel ? " open" : "")
        }
      >
        <button
          className="applications-menu"
          onClick={() => dispatch(toggleAppMenu())}
        >
          Apps
        </button>
        <div className="panel-apps">
          {Object.keys(apps).map((item, index) => {
            if (apps[item].pinned) {
              return (
                <OverlayTrigger
                  key={item}
                  placement="right"
                  delay={{ show: 50, hide: 50 }}
                  overlay={
                    <Tooltip id="button-tooltip-2">
                      {apps[item].app_name}
                    </Tooltip>
                  }
                >
                  <button
                    className={
                      "panel-icon " +
                      item +
                      " " +
                      (apps[item].alive ? "alive" : " ") +
                      (apps[item].hidden ? " hidden" : " ") +
                      (item == activeWindow ? " active" : "")
                    }
                    onClick={() => activateApp(item)}
                    onWheel={() =>
                      apps[item].alive && !apps[item].hidden
                        ? dispatch(setAppFocus(item))
                        : apps[item].alive
                        ? dispatch(showApp(item))
                        : null
                    }
                  ></button>
                </OverlayTrigger>
              );
            }
          })}
        </div>
        <div
          className={
            "panel-apps" +
            (Object.keys(apps).filter(
              (item) => !apps[item].pinned && apps[item].alive
            ).length > 0
              ? " hide"
              : 0)
          }
        >
          {Object.keys(apps).map((item, index) => {
            if (!apps[item].pinned && apps[item].alive) {
              return (
                <OverlayTrigger
                  key={item}
                  placement="right"
                  delay={{ show: 50, hide: 50 }}
                  overlay={
                    <Tooltip id="button-tooltip-2">
                      {apps[item].app_name}
                    </Tooltip>
                  }
                >
                  <button
                    className={
                      "panel-icon " +
                      item +
                      " " +
                      (apps[item].alive ? "alive" : " ") +
                      (apps[item].hidden ? " hidden" : " ") +
                      (item == activeWindow ? " active" : "")
                    }
                    onClick={() => activateApp(item)}
                    onWheel={() =>
                      apps[item].alive && !apps[item].hidden
                        ? dispatch(setAppFocus(item))
                        : apps[item].alive
                        ? dispatch(showApp(item))
                        : null
                    }
                  ></button>
                </OverlayTrigger>
              );
            }
          })}
        </div>
      </div>
      <div
        className="side-panel-trigger-box"
        onMouseEnter={() => setForceOpenSidePanel(true)}
        onMouseLeave={() => setForceOpenSidePanel(false)}
      ></div>
    </div>
  );
}

export default Panel;
