import "./RootWindow.css";
import { useEffect, useState } from "react";
import Splash from "./Splash";
import Desktop from "./windows/desktop/Desktop";
import Panel from "./windows/panel/Panel";
import WelcomeApp from "./apps/welcome/WelcomeApp";
import HelpApp from "./apps/help/HelpApp";
import ApplicationMenu from "./windows/application-menu/ApplicationMenu";
import { useDispatch, useSelector } from "react-redux";
import { launchApp } from "./redux/reducers/appStatusReducer";
import ShadowCompositor from "./compositors/shadow.compositor";
import GnomeWebApp from "./apps/gnomeWebApp/GnomeWebApp";
import FilesApp from "./apps/filesApp/FilesApp";

function RootWindow() {
  // Boot loader
  const [showSplash, setShowSplash] = useState(true);

  // AppStatus
  // const apps = useSelector((state) => state.appState.value.apps);
  const useCompositor = useSelector((state) => state.canvasCompositor.value);
  const dispatch = useDispatch();

  setTimeout(() => {
    setShowSplash(false);
  }, 8000);

  useEffect(() => {
    setTimeout(() => {
      console.log("launching");
      dispatch(launchApp("welcomeApp"));
    }, 10000);
  }, []);

  return (
    <>
      <div className="root-window">
        {/* Desktop Decorations */}
        <Desktop />
        <Panel />
        <ApplicationMenu />
        {useCompositor && <ShadowCompositor />}

        {/* Apps */}
        <WelcomeApp />
        <HelpApp />
        <GnomeWebApp />
        <FilesApp />
      </div>
      {showSplash ? <Splash /> : null}
    </>
  );
}

export default RootWindow;
