import { useEffect, useRef, useState } from "react";
import { useSelector } from "react-redux";

function ShadowCompositor() {
  const [canvasWidth, setCanvasWidth] = useState(window.innerWidth);
  const [canvasHeight, setCanvasHeight] = useState(window.innerHeight);
  CanvasRenderingContext2D.prototype.roundRect = function (x, y, w, h, r) {
    if (w < 2 * r) r = w / 2;
    if (h < 2 * r) r = h / 2;
    this.beginPath();
    this.moveTo(x + r, y);
    this.arcTo(x + w, y, x + w, y + h, r);
    this.arcTo(x + w, y + h, x, y + h, r);
    this.arcTo(x, y + h, x, y, r);
    this.arcTo(x, y, x + w, y, r);
    this.closePath();
    return this;
  };
  CanvasRenderingContext2D.prototype.roundedShadow = function (
    x,
    y,
    width,
    height,
    radius,
    blur
  ) {
    this.shadowBlur = blur;
    this.shadowOffsetY = blur / 2;
    this.shadowColor = "rgb(40,40,40)";
    this.fillStyle = "rgb(40,40,40)";
    this.roundRect(x, y, width, height, radius).fill();
    this.fill();
    this.shadowBlur = blur / 3;
    this.shadowColor = "white";
    this.fillStyle = "white";
    this.roundRect(x, y, width, height - blur / 3, radius).fill();
    this.fill();
  };

  const canvasRef = useRef(null);
  useEffect(() => {
    window.addEventListener("resize", () => {
      setCanvasWidth(window.innerWidth);
      setCanvasHeight(window.innerHeight);
    });
  }, []);

  const apps = useSelector((state) => state.appState.value.apps);
  const appMenuOpen = useSelector((state) => state.appMenu.value);

  const activeWindow = useSelector(
    (state) => state.appState.value.activeWindow
  );

  function draw() {
    let app_names = Object.keys(apps).filter(
      (x) => apps[x].alive && !apps[x].hidden
    );
    // console.log(apps[app_names[0]])
    app_names.sort((x, y) => (apps[x].order > apps[y].order ? 1 : -1));
    let n = app_names.length;
    // console.log(app_names);
    const ctx = canvasRef.current.getContext("2d");
    ctx.clearRect(0, 0, window.innerWidth, window.innerHeight);
    let app_window = null;
    for (let i = 0; i < n; i++) {
      app_window = apps[app_names[i]];
      if (app_window) {
        if (activeWindow == app_names[i]) {
          if (!app_window.fullscreen) {
            ctx.roundedShadow(
              app_window.x,
              app_window.y,
              app_window.widthActual,
              app_window.heightActual,
              8,
              24
            );
          } else {
            ctx.fillStyle = "white";
            ctx.fillRect(24, 0, window.innerWidth, window.innerHeight - 24);
          }
        } else {
          if (!app_window.fullscreen) {
            ctx.roundedShadow(
              app_window.x + (app_window.widthActual / 2) * 0.02,
              app_window.y + (app_window.heightActual / 2) * 0.02,
              app_window.widthActual * 0.98,
              app_window.heightActual * 0.98,
              8,
              8
            );
          }
        }
      }
    }
  }

  useEffect(() => {
    if (!appMenuOpen) draw();
  }, [apps, appMenuOpen]);

  return (
    <div
      className="shadow-compositor-window"
      style={{
        position: "fixed",
        pointerEvents: "none",
        top: 0,
        left: 0,
        bottom: 0,
        right: 0,
        mixBlendMode: "multiply",
        zIndex: 98,
      }}
    >
      <canvas
        ref={canvasRef}
        width={canvasWidth}
        height={canvasHeight}
        style={{ visibility: appMenuOpen ? "hidden" : "visible" }}
      ></canvas>
    </div>
  );
}

export default ShadowCompositor;
