import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setVolume } from "../redux/reducers/audioVolumeReducer";
import "./Slider.css";

function Slider(props) {
  const [subClass, setSubClass] = useState("");
  const volume = useSelector((state) => state.audioVolume.value);
  const dispatch = useDispatch();

  function changeHandler(value) {
    if (props.baseClass == "audio-output") {
      dispatch(setVolume(value));
      if (value <= 1) {
        setSubClass(" zero");
      } else if (value < 33) {
        setSubClass(" low");
      } else if (value < 66) {
        setSubClass(" medium");
      } else {
        setSubClass("");
      }
    }
  }

  return (
    <div className="slider">
      <input
        id={props.id}
        type="range"
        min="1"
        max="100"
        defaultValue={
          props.baseClass == "audio-output" ? volume : props.defaultValue
        }
        onChange={(e) => {
          changeHandler(e.target.value);
        }}
      ></input>
      <div
        style={{ position: "relative" }}
        className={props.baseClass + subClass}
      ></div>
    </div>
  );
}

export default Slider;
