import { configureStore } from '@reduxjs/toolkit'
import appMenuReducer from './reducers/appMenuReducer'
import appStatusReducer from './reducers/appStatusReducer'
import audioVolumeReducer from './reducers/audioVolumeReducer'
import canvasCompositorReducer from './reducers/canvasCompositorReducer'
import wallpaperReducer from './reducers/wallpaperReducer'

export default configureStore({
  reducer: {
    appMenu: appMenuReducer,
    appState: appStatusReducer,
    canvasCompositor: canvasCompositorReducer,
    audioVolume: audioVolumeReducer,
    wallpaper: wallpaperReducer
  },
})