import { createSlice } from "@reduxjs/toolkit";

export const canvasCompositorSlice = createSlice({
  name: "canvasCompositor",
  initialState: {
    value: false,
  },
  reducers: {
    enableCompositor: (state) => {
      state.value = true;
    },
    disableCompositor: (state) => {
      state.value = false;
    },
    toggleCompositor: (state) => {
      state.value = !state.value;
    }
  }
});

export const { enableCompositor, disableCompositor, toggleCompositor } = canvasCompositorSlice.actions;

export default canvasCompositorSlice.reducer;