import "./TopLevelWindow.css";
import { useEffect, useRef, useState } from "react";
import { DraggableCore } from "react-draggable";
import { useDispatch, useSelector } from "react-redux";
import {
  closeApp,
  setAppActualSize,
  setAppFocus,
  setAppPosition,
  setAppSize,
  setMinAppSize,
  toggleFullScreen,
} from "../../redux/reducers/appStatusReducer";
import { CSSTransition } from "react-transition-group";
import GlassCompositor from "../../compositors/glass.compositor";

function TopLevelWindow(props) {
  const dispatch = useDispatch();
  const [currentMousePos, setCurrentMousePos] = useState({ x: 0, y: 0 });
  const [currentResizeTarget, setCurrentResizeTarget] = useState("");
  const compositorEnabled = useSelector((state) => {
    return state.canvasCompositor.value;
  });
  const apps = useSelector((state) => {
    return state.appState.value.apps;
  });
  const activeWindow = useSelector((state) => {
    return state.appState.value.activeWindow;
  });

  const nodeRef = useRef(null);

  useEffect(() => {
    dispatch(
      setAppPosition({
        app_name: props.id,
        x:
          apps[props.id].xRelative * window.innerWidth -
          apps[props.id].width / 2,
        y:
          apps[props.id].yRelative * window.innerHeight -
          apps[props.id].height / 2,
      })
    );
  }, []);

  function handleStart(e) {
    setCurrentMousePos({ x: e.clientX, y: e.clientY });
  }

  function handleDrag(e) {
    if (!apps[props.id].fullscreen) {
      dispatch(
        setAppPosition({
          app_name: props.id,
          x: apps[props.id].x + (e.clientX - currentMousePos.x),
          y:
            apps[props.id].y + (e.clientY - currentMousePos.y) > 23
              ? apps[props.id].y + (e.clientY - currentMousePos.y)
              : 24,
        })
      );

      currentMousePos.x = e.clientX;
      currentMousePos.y = e.clientY;
    }
  }

  function handleResizeStart(e) {
    setCurrentResizeTarget(e.target.id);
    setCurrentMousePos({ x: e.clientX, y: e.clientY });
  }

  function handleResizeMove(e) {
    if (!apps[props.id].fullscreen) {
      if (currentResizeTarget == "bottom") {
        let newHeight = apps[props.id].height + (e.clientY - currentMousePos.y);
        dispatch(
          setAppSize({
            app_name: props.id,
            height:
              newHeight > apps[props.id].minHeight
                ? newHeight
                : apps[props.id].minHeight,
            width: apps[props.id].width,
          })
        );
      } else if (currentResizeTarget == "top") {
        let newHeight = apps[props.id].height - (e.clientY - currentMousePos.y);
        let newPos = apps[props.id].y + (e.clientY - currentMousePos.y);
        if (newHeight > apps[props.id].minHeight && newPos > 23) {
          dispatch(
            setAppPosition({
              app_name: props.id,
              x: apps[props.id].x,
              y: newPos,
            })
          );
          dispatch(
            setAppSize({
              app_name: props.id,
              height:
                newHeight > apps[props.id].minHeight
                  ? newHeight
                  : apps[props.id].minHeight,
              width: apps[props.id].width,
            })
          );
        }
      } else if (currentResizeTarget == "right") {
        let newWidth = apps[props.id].width + (e.clientX - currentMousePos.x);
        dispatch(
          setAppSize({
            app_name: props.id,
            width:
              newWidth > apps[props.id].minWidth
                ? newWidth
                : apps[props.id].minWidth,
            height: apps[props.id].height,
          })
        );
      } else if (currentResizeTarget == "left") {
        let newWidth = apps[props.id].width - (e.clientX - currentMousePos.x);
        if (newWidth > apps[props.id].minWidth) {
          dispatch(
            setAppPosition({
              app_name: props.id,
              x: apps[props.id].x + (e.clientX - currentMousePos.x),
              y: apps[props.id].y,
            })
          );
          dispatch(
            setAppSize({
              app_name: props.id,
              width: newWidth,
              height: apps[props.id].height,
            })
          );
        }
      } else if (currentResizeTarget == "bottom-right") {
        let newWidth = apps[props.id].width + (e.clientX - currentMousePos.x);
        let newHeight = apps[props.id].height + (e.clientY - currentMousePos.y);
        dispatch(
          setAppSize({
            app_name: props.id,
            width:
              newWidth > apps[props.id].minWidth
                ? newWidth
                : apps[props.id].minWidth,
            height:
              newHeight > apps[props.id].minHeight
                ? newHeight
                : apps[props.id].minHeight,
          })
        );
      } else if (currentResizeTarget == "bottom-left") {
        let newWidth = apps[props.id].width - (e.clientX - currentMousePos.x);
        let newHeight = apps[props.id].height + (e.clientY - currentMousePos.y);
        dispatch(
          setAppPosition({
            app_name: props.id,
            x:
              newWidth > apps[props.id].minWidth
                ? apps[props.id].x + (e.clientX - currentMousePos.x)
                : apps[props.id].x,
            y: apps[props.id].y,
          })
        );
        dispatch(
          setAppSize({
            app_name: props.id,
            width:
              newWidth > apps[props.id].minWidth
                ? newWidth
                : apps[props.id].minWidth,
            height:
              newHeight > apps[props.id].minHeight
                ? newHeight
                : apps[props.id].minHeight,
          })
        );
      } else if (currentResizeTarget == "top-right") {
        let newWidth = apps[props.id].width + (e.clientX - currentMousePos.x);
        let newHeight = apps[props.id].height - (e.clientY - currentMousePos.y);
        let newPos = apps[props.id].y + (e.clientY - currentMousePos.y);
        dispatch(
          setAppPosition({
            app_name: props.id,
            x: apps[props.id].x,
            y:
              newHeight > apps[props.id].minHeight && newPos > 23
                ? apps[props.id].y + (e.clientY - currentMousePos.y)
                : apps[props.id].y,
          })
        );
        dispatch(
          setAppSize({
            app_name: props.id,
            width:
              newWidth > apps[props.id].minWidth
                ? newWidth
                : apps[props.id].minWidth,
            height:
              newHeight > apps[props.id].minHeight
                ? newHeight
                : apps[props.id].minHeight,
          })
        );
      } else if (currentResizeTarget == "top-left") {
        let newWidth = apps[props.id].width - (e.clientX - currentMousePos.x);
        let newHeight = apps[props.id].height - (e.clientY - currentMousePos.y);
        let newPos = apps[props.id].y + (e.clientY - currentMousePos.y);
        dispatch(
          setAppPosition({
            app_name: props.id,
            x:
              newWidth > apps[props.id].minWidth
                ? apps[props.id].x + (e.clientX - currentMousePos.x)
                : apps[props.id].x,
            y:
              newHeight > apps[props.id].minHeight && newPos > 23
                ? apps[props.id].y + (e.clientY - currentMousePos.y)
                : apps[props.id].y,
          })
        );
        dispatch(
          setAppSize({
            app_name: props.id,
            width:
              newWidth > apps[props.id].minWidth
                ? newWidth
                : apps[props.id].minWidth,
            height:
              newHeight > apps[props.id].minHeight
                ? newHeight
                : apps[props.id].minHeight,
          })
        );
      }

      currentMousePos.x = e.clientX;
      currentMousePos.y = e.clientY;
    }
  }

  function focusHandler() {
    dispatch(setAppFocus(props.id));
  }

  function setActualAppSize() {
    if (nodeRef.current && apps[props.id].alive && !apps[props.id].hidden) {
      dispatch(
        setAppActualSize({
          app_name: props.id,
          width: nodeRef.current.clientWidth,
          height: nodeRef.current.clientHeight,
        })
      );
    }
  }

  useEffect(() => {
    setActualAppSize();
  }, [
    nodeRef.current ? nodeRef.current.clientWidth : 0,
    nodeRef.current ? nodeRef.current.clientHeight : 0,
  ]);

  return (
    <DraggableCore
      handle=".window-title"
      onStart={handleStart}
      onDrag={handleDrag}
    >
      <CSSTransition
        in={apps[props.id].alive}
        nodeRef={nodeRef}
        timeout={300}
        unmountOnExit
        onEnter={focusHandler}
        classNames="window"
      >
        <CSSTransition
          in={!apps[props.id].hidden}
          nodeRef={nodeRef}
          timeout={300}
          onEnter={focusHandler}
          classNames="window-to-panel"
        >
          <div
            ref={nodeRef}
            id={props.id}
            className={
              "top-level-window" +
              (apps[props.id].fullscreen ? " fullscreen" : "") +
              (props.id !== activeWindow ? " dimmed" : "") +
              (compositorEnabled ? " composited" : "") +
              (apps[props.id].hidden ? " window-inside-panel" : "")
            }
            style={{
              ...{ left: apps[props.id].x, top: apps[props.id].y },
              ...{ zIndex: apps[props.id].order },
              ...{
                minWidth: apps[props.id].fullscreen
                  ? window.innerWidth
                  : apps[props.id].width,
                minHeight: apps[props.id].fullscreen
                  ? window.innerHeight - 76
                  : apps[props.id].height,
              },
              ...{
                width: apps[props.id].width,
                height: "fit-content",
              },
            }}
            onMouseDownCapture={focusHandler}
          >
            {/* Composited Blur */}
            {compositorEnabled && <GlassCompositor id={props.id} />}
            {/* Window Decorations */}
            <div id="headerbar">
              <button
                className="headerbar-button headerbar-left"
                onClick={($event) => {
                  dispatch(closeApp(props.id));
                  $event.stopPropagation();
                }}
              ></button>
              <div
                className="window-title"
                style={{
                  paddingRight: apps[props.id].canFullScreen ? 8 : 32,
                }}
                onDoubleClick={() => dispatch(toggleFullScreen(props.id))}
              >
                {props.customHeaderbar && <>{props.customHeaderbar}</>}
                {!props.customHeaderbar && <>{props.title}</>}
              </div>
              {apps[props.id]["canFullScreen"] && (
                <button
                  className="headerbar-button headerbar-right"
                  onClick={() => dispatch(toggleFullScreen(props.id))}
                ></button>
              )}
            </div>
            <div className={props.flap ? 'flex-container' : (props.type == 'modal-window' ? '' : 'default-container')}>
              {apps[props.id].resizable && (
                <>
                  <DraggableCore
                    handle="#top"
                    onStart={handleResizeStart}
                    onDrag={handleResizeMove}
                  >
                    <div className="window-resize-handle" id="top"></div>
                  </DraggableCore>
                  <DraggableCore
                    handle="#bottom"
                    onStart={handleResizeStart}
                    onDrag={handleResizeMove}
                  >
                    <div className="window-resize-handle" id="bottom"></div>
                  </DraggableCore>
                  <DraggableCore
                    handle="#left"
                    onStart={handleResizeStart}
                    onDrag={handleResizeMove}
                  >
                    <div className="window-resize-handle" id="left"></div>
                  </DraggableCore>
                  <DraggableCore
                    handle="#right"
                    onStart={handleResizeStart}
                    onDrag={handleResizeMove}
                  >
                    <div className="window-resize-handle" id="right"></div>
                  </DraggableCore>
                  <DraggableCore
                    handle="#top-left"
                    onStart={handleResizeStart}
                    onDrag={handleResizeMove}
                  >
                    <div className="window-resize-handle" id="top-left"></div>
                  </DraggableCore>
                  <DraggableCore
                    handle="#top-right"
                    onStart={handleResizeStart}
                    onDrag={handleResizeMove}
                  >
                    <div className="window-resize-handle" id="top-right"></div>
                  </DraggableCore>
                  <DraggableCore
                    handle="#bottom-left"
                    onStart={handleResizeStart}
                    onDrag={handleResizeMove}
                  >
                    <div
                      className="window-resize-handle"
                      id="bottom-left"
                    ></div>
                  </DraggableCore>
                  <DraggableCore
                    handle="#bottom-right"
                    onStart={handleResizeStart}
                    onDrag={handleResizeMove}
                  >
                    <div
                      className="window-resize-handle"
                      id="bottom-right"
                    ></div>
                  </DraggableCore>
                </>
              )}
              {/* Window Sidepane */}
              {props.flap && <div className="flap">{props.flap}</div>}
              {/* Window Contents */}
              <div className={"window-content " + props.type} id="content">
                {props.children}
              </div>
            </div>
          </div>
        </CSSTransition>
      </CSSTransition>
    </DraggableCore>
  );
}

export default TopLevelWindow;
