import { useEffect, useRef, useState } from "react";
import { useSelector } from "react-redux";

import "./glass-alt.css";

function GlassCompositor(props) {
  const [canvasWidth, setCanvasWidth] = useState(window.innerWidth);
  const [canvasHeight, setCanvasHeight] = useState(window.innerHeight);
  const [offScreenCanvas, setOffScreenCanvas] = useState(null);
  const [textureReady, setTextureReady] = useState(false)
  const divRef = useRef(null);
  const canvasRef = useRef(null);
  const app = useSelector((state) => state.appState.value.apps[props.id]);
  const activeWindow = useSelector(
    (state) => state.appState.value.activeWindow
  );

  const wallpaper = useSelector((state) => state.wallpaper.value);

  function loadImage() {
    if (offScreenCanvas == null) {
      let off = document.createElement("canvas");
      setOffScreenCanvas(off);
      let img = new Image();
      img.onload = function () {
        off.width = window.innerWidth;
        off.height = window.innerHeight;
        let _ctx = off.getContext("2d");
        _ctx.filter = "blur(32px)";
        _ctx.drawImage(img, 0, 0, window.innerWidth, window.innerHeight);
        setTextureReady(true);
      };
      img.src =
        window.location.href.substring(0, window.location.href.length - 1) +
        require("../assets/data/_Home/_Pictures/" + wallpaper);
    }
  }

  useEffect(() => {
    loadImage();
  }, [offScreenCanvas, wallpaper]);

  function draw() {
    canvasRef.current.width = app.widthActual;
    canvasRef.current.height = app.heightActual;
    const ctx = canvasRef.current.getContext("2d");
    ctx.drawImage(
      offScreenCanvas,
      -app.x,
      -app.y,
      window.innerWidth,
      window.innerHeight
    );
  }

  useEffect(() => {
    if (offScreenCanvas) draw();
  }, [app, activeWindow,textureReady]);

  return (
    <div
      ref={divRef}
      className="glass-compositor"
      style={{
        position: "absolute",
        top: 0,
        bottom: 0,
        left: 0,
        right: 0,
        pointerEvents: "none",
        zIndex: -1,
        borderRadius: 8,
        overflow: "hidden",
      }}
    >
      <div
        className={
          "canvas-background" + (props.id == activeWindow ? "" : " dimmed")
        }
      ></div>
      <canvas
        style={{ width: "100%", height: "100%" }}
        className="canvas-backdrop"
        ref={canvasRef}
        width={canvasWidth}
        height={canvasHeight}
      ></canvas>
    </div>
  );
}

export default GlassCompositor;
