import { createSlice } from '@reduxjs/toolkit'

export const appMenuSlice = createSlice({
  name: 'appMenu',
  initialState: {
    value: false
  },
  reducers: {
    openAppMenu: (state) => {
      state.value = true
    },
    closeAppMenu: (state) => {
      state.value = false
    },
    toggleAppMenu: (state) => {
      console.log("Start!")
      state.value = !state.value
    }
  }
});

export const { openAppMenu, closeAppMenu, toggleAppMenu } = appMenuSlice.actions;

export default appMenuSlice.reducer;